import React from 'react';
import ReactPlayer from 'react-player'
class Movie extends React.Component{

    render() {
        const {title, duration, userRating, desc, stars, genre, poster, video} = this.props.location.state.movieProps;
        return (
            <div className="movie">
                <img src={poster} alt={`${title} Poster`}/>
                <div className="movieInfo">
                    <h1>{title}</h1>
                    <p><strong>Puntuación: </strong>{userRating}</p>
                    <p><strong>Duración: </strong>{duration}</p>
                    <p><strong>Reparto: </strong>{stars}</p>
                    <p><strong>Género: </strong>{genre}</p>
                    <p><strong>Descripción: </strong>{desc}</p>
					<ReactPlayer url={video} playing controls />
                </div>
            </div>
        )
    }
}

export default Movie;


