import React from 'react';
import Stepbrothers from '../../img/carousel/stepbrothers.jpg';

class Second extends React.Component{
    render() {
        const stepbrothers = {
            background: `url(${Stepbrothers})`
        };
        return(
            <div className="carousel-item" style={stepbrothers}>
                <div className="box-left"></div>
                <div className="movie-image" style={stepbrothers}></div>
                <div className="title-info">
                    <h2>Hermanos por pelotas</h2>
                    <h3>92% Match 2008 R</h3>
                    <p className="overview">Aunque Brennan Huff (Will Ferrell) tiene casi cuarenta años, sigue viviendo con su madre Nancy (Mary Steenburgen) y se conforma con conseguir, de vez en cuando, empleos esporádicos. Dale Doback (John C. Reilly), es un parado profesional de cuarenta años que vive con su padre, Robert (Richard Jenkins).</p>

                    <div className="up-down">
                        <i className="fa fa-thumbs-o-up" aria-hidden="true"></i>
                        <i className="fa fa-thumbs-o-down" aria-hidden="true"></i>
                    </div>

                    <p className="overview">Reparto:
                        <span className="hover"> Will Ferrell</span>,
                        <span className="hover"> John C. Reilly</span>,
                        <span className="hover"> Mary Steenburgen</span>
                    </p>

                    <p className="overview">Género:
                        <span className="hover"> Comedia</span>,
                        <span className="hover"> Comedia Familiar</span>,
                        <span className="hover"> Intriga</span>
                    </p>
                </div>
            </div>
        )
    }
}

export default Second;


