import React from 'react';
import Interstellar from '../../img/carousel/intertellar.jpg';

class First extends React.Component{
    render() {
        const interstellar = {
            background: `url(${Interstellar})`
        };
        return(
            <div className="carousel-item" >
                <div className="box-left"></div>
                <div className="movie-image" style={interstellar}></div>
                <div className="title-info">
                    <h2>Interstellar</h2>
                    <h3>96% Match 2014 PG-13</h3>
                    <p className="overview">Al ver que la vida en la Tierra está llegando a su fin, un grupo de exploradores dirigidos por el piloto Cooper (McConaughey) y la científica Amelia (Hathaway) emprende una misión que puede ser la más importante de la historia de la humanidad: viajar más allá de nuestra galaxia para descubrir algún planeta en otra que pueda garantizar el futuro de la raza humana.</p>

                    <div className="up-down">
                        <i className="fa fa-thumbs-o-up" aria-hidden="true"></i>
                        <i className="fa fa-thumbs-o-down" aria-hidden="true"></i>

                    </div>

                    <p className="overview">Reparto:
                        <span className="hover"> Mathew McConaughey</span>,
                        <span className="hover"> Anne Hathaway</span>,
                        <span className="hover"> Jessica Chastain</span>
                    </p>

                    <p className="overview">Género:
                        <span className="hover"> Ciencia Ficción</span>,
                        <span className="hover"> Aventras</span>,
                        <span className="hover"> Drama</span>
                    </p>
                </div>
            </div>
        )
    }
}

export default First;


