import React from 'react';
import Logo from '../img/Netflix-logo.png';
import { Link } from 'react-router-dom';

class Header extends React.Component{
    render(){
        return (
          <div className="header">
              <Link to="/netflix-react/">
                  <img src={Logo} alt='Netflix Logo'/>
              </Link>
              <nav>
                  <ul>
                      <li><Link to="/netflix-react/"><strong>Home</strong></Link></li>
                      <li><a>Shows de TV</a></li>
                      <li><a>Películas</a></li>
                      <li><a>Originales</a></li>
                      <li><a>Añadidos recientemente</a></li>
                      <li><a>Mi Lista</a></li>
                      <li><Link to="/netflix-react/registration">Registrarse</Link></li>
                  </ul>
              </nav>

          </div>
        );
    }
}

export default Header;


